<script setup>
import { ref, inject, onMounted } from "vue";
import * as utService from '@/services/utilitiesService';

var loader = inject("loader");

const member = ref({
    organizationId: null,
    sectionId: null,
    itsId: null,
    hofId: null,
    sfNo: null,
    fullName: null,
    homeAddress: null,
    dateOfBirth: null,
    maritalStatus: null,
    houseStatus: null,
    bloodGroup: null,
    weight: null,
    height: null,
    otherHealthIssues: null,
});
const orgs = ref([]);
const sections = ref([]);

const validations = {
    itsId: [
        val => {
            if (val)
                return true;

            return "Its number is required";
        }
    ],
    hofId: [],
    sfNo: [],
    fullName: [],
    dob: [],
    team: [],
    section: [],
};

onMounted(async () => {
    var promises = [
        utService.getOrganizations(5),
        utService.getSections()
    ];

    loader.show();

    const [getOrgs, getSections] = await Promise.all(promises);

    orgs.value = getOrgs;
    sections.value = getSections;

    loader.hide();

    member.value.organizationId = orgs.value[0].organizationId;
});
